.image-cover:before {
   content: "";
   display: block;
   padding-top: 150%;
}
.image-cover.greenhouse_author-cover:before {
   padding-top: 100%;
}
.image-cover.greenhouse_author-cover, .swiper-slide.type-greenhouse_author {
    position: relative;
    border-radius: 100%;
    margin-bottom: 150px;
    h3 {
        text-align: left;
        transition: color 0.2s;
    }
    a {
        display: block;
    }
    &:hover h3 {
        color: $font-colour-highlight;
    }
    .overlay {
        position: absolute;
        top: $site-padding * 2;
        left: 0;
        width: 100%;
        margin-top: 100%;
        padding-top: $site-padding / 2;
        border-top: solid 1px $border-colour-primary;
        p, hr {
            display: none;
        }
        .content {
            padding: $site-padding 0;
        }
    }
}
.grid-article { position: relative; }
.swiper-greenhouse_author { padding-bottom: 0; }
.swiper-greenhouse_author .swiper-button-prev, .swiper-greenhouse_author .swiper-button-next { top: 37%; }
.image-cover.book-cover {
    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        padding: $site-padding;
        opacity: 0;
        height: 100%;
        width: 100%;
        color: $font-colour-primary;
        transition: opacity 0.3s;
        .content {
            height: 100%;
            background: map-get($colours, 'white');
            border: solid 1px $border-colour-primary;
            padding: $site-padding * 2;
        }
        .overflow-hidden {
            padding: 0;
            opacity: 0;
            transition: opacity 0.3s, transform 0.3s;
            transform: translateY(20px);
        }
        hr {
            margin: $site-padding 0;
        }
    }
    &:hover .overlay, &.no-image .overlay {
        opacity: 1;
        .overflow-hidden {
            opacity: 1;
            transform: translateY(0);
        }
    }
}
.image--circle {
    position: relative;
    border-radius: 100%;
}
.image-zoom {
    transition: transform 0.2s;
    &:hover {
        transform: scale(1.05);
    }
}
.grid--awards {
    .content--awards { border-bottom: solid 1px $border-colour-primary; }
    &:last-of-type .content--awards {
        border-bottom: none;
    }
}
.nav-filter {
    li .content {
        padding: 12px;
    }
    @include respond-to($max, medium) {
        li {
            flex-basis: 200px;
        }
    }
}
