//Helpers can be used to align content, or assist for various layout.
.set-center { text-align: center; }
.grid.set-center { justify-content: center; }
.set-right { text-align: right; }
.grid.set-right { justify-content: flex-end; }
.set-left { text-align: left; }
@media screen and (max-width: $medium) {
	.medium--set-right {
		text-align: right;
	}
	.medium--set-left {
		text-align: left;
	}
}
@media screen and (max-width: $small) {
	.small--set-right {
		text-align: right;
	}
	.small--set-left {
		text-align: left;
	}
}

.grid--match-heights .content {display: table-row;} // Add match-height class to any grid item to match the columns height inside.

.set-bottom { vertical-align: bottom; }
.set-top { vertical-align: top; } 

.set-start { justify-content: flex-start; }
.set-end { justify-content: flex-end; }
.grid.set-end { align-items: flex-end; }
.grid.set-middle { align-items: center; }
.grid.set-baseline { align-items: baseline; }

.limit-to-site-width {
	max-width: $site-width - ($site-padding * 4);
	margin: 0 auto;
}

.vertical-center {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;	
	z-index: 100;
    justify-content: center;
    align-items: center; 
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    -ms-flex-pack: center;
    -ms-flex-direction: column;
    -ms-flex-wrap: wrap;
    -ms-flex-align: stretch;
    @media screen and (max-width: $medium) {
		top: 0;
		height: auto;
		position: relative;    
	}
}
.vertical-bottom {
	position: absolute;
	bottom: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

// Set padding map, this is the same as using a spacer but without having to add additional markup.
$paddings: (	
	'sp':		$site-padding,
	'0':		0,
	'5':		5px,
	'10':		10px,
	'15':		15px,
	'20':		20px,
	'25':		25px,
	'30':		30px,
	'40':		40px,
	'50':		50px,
	'60':		60px
);
@each $pad, $padVal in $paddings {
    .set-pad-#{$pad} {
        padding: $padVal;
    }
    .set-pad-#{$pad}-0 {
        padding: $padVal 0;
    }
    .set-pad-#{$pad}-i {
        padding-top: $padVal;
        padding-bottom: $padVal;
    }
    .set-pad-top-#{$pad} {
        padding-top: $padVal;
    }
    .set-pad-bottom-#{$pad} {
        padding-bottom: $padVal;
    }  
    @include respond-to($max, medium) {
	    // By default we halve the value for smaller devices.
	    .set-pad-#{$pad} {
			padding: ($padVal / 2); 
		}
		.set-pad-#{$pad}-0 {
	        padding: ($padVal / 2) 0;
	    }
		.set-pad-top-#{$pad} {
        	padding-top: ($padVal / 2);
	    }
	    .set-pad-bottom-#{$pad} {
	        padding-bottom: ($padVal / 2);
	    }
	}
}

//Generate colour sets for use elsewhere.
@each $class, $colour, $text-colour in $colours-setup {
    .#{$class} {
		background-color: $colour;
		@if $text-colour {
			color: $text-colour;
			a {
				color: $text-colour;
			}
		}
    }
}

// Image related helpers
.image--full-width {
	width: 100%;
}