.form-field {
	padding: $site-padding;
}
input, textarea, button {
	width: 100%;
	background:  map-get($colours, 'white');
	border: solid 1px #eee;
	font-size: 14px;
	padding: $site-padding;
	&:focus {
		outline: none;
		border: solid 1px  $background-colour-alternative;
	}
}
input[type="submit"] {
	cursor: pointer;
	background: $background-colour-primary;
	color:  map-get($colours, 'white');
}
label {
	display: block;
	font-size: 16px;
	padding: $site-padding 0;
	span {
		color: #222;
	}
}

.button {
	display: inline-block;
	padding: $site-padding;
	cursor: pointer;
	background: $background-colour-primary;
	color:  map-get($colours, 'white');
	&:hover {
		background: lighten($background-colour-primary, 20%);
	}
}
.button--bottom {
	position: absolute;
	bottom: $site-padding;
}
.button {
    background: map-get($colours, 'white');
    border: solid 1px $background-colour-highlight;
    color: $font-colour-highlight;
    font-size: $font-small;
    transition: background 0.2s;
    &.button--active {
        background: $background-colour-highlight;
        border: none;
        padding: $site-padding + 1;
        color: map-get($colours, 'white');
    }
}
li .button {
    display: block;
}
.button--solid {
    background: $background-colour-highlight;
    border: solid 1px $background-colour-highlight;
    color: map-get($colours, 'white');
    a {
        color: map-get($colours, 'white');
    }
    &:hover {
        @extend .button;
        padding: $site-padding / 2 - 1;
        a {
            color: $font-colour-highlight;
        }
    }
}
.button--slim {
    padding: $site-padding / 2;
}
.button--solid.button--slim:hover {
    background: map-get($colours, 'white');
    border: solid 1px $background-colour-highlight;
    color: $font-colour-highlight;
    padding: $site-padding / 2;
}
.button--large {
    padding: $site-padding * 2;
    text-align: center;
    text-transform: uppercase;
    &:hover {
        padding: $site-padding * 2;
    }
}
.button--transparent {
    background: none;
    color: map-get($colours, 'white');
    border: solid 1px map-get($colours, 'white');
}
.button--round {
    display: block;
    border-radius: 20px;
    border-width: 3px;
    color: map-get($colours, 'black');
    &:hover {
        background: $background-colour-highlight;
        color: map-get($colours, 'white');
    }
}

.pp-table td {
    border: solid 1px;
    padding: 2px;
}
