.line-through {
    &::after {
        content:'';
        border-top: solid 1px map-get($colours, 'grey');
        position: absolute;
        width: 100%;
        top: 52%;
        left: 0;
    }
    h3 {
        position: relative;
        background: map-get($colours, 'white');
        font-size: ($font-size-heading);
        display: inline;
        z-index: 50;
        padding: $site-padding;
    }
}
.content--book {
    h2 {
        padding-bottom: $site-padding * 3.5;
    }
    h3 {
        padding-bottom: $site-padding * 2.5;
    }
}
.location-switch {
    margin-bottom: $site-padding * 4;
    li {
        background: map-get($colours, 'white');
        color: $font-colour-highlight;
        font-size: $font-smaller;
        width: 50%;
        text-transform: uppercase;
        padding: $site-padding 0;
        border: solid 1px $background-colour-highlight;
        text-align: center;
        cursor: pointer;
        &.active {
            background: $background-colour-highlight;
            color: map-get($colours, 'white');
            border: none;
        }
    }
}
.book-image {
    img {
        border-radius: 5px;
    }
}
.book-image.hide {
    display: none;
}