//Any section specific functionality

main > section, main > .section {
	position: relative;
	width: 100%;
	padding: ($site-padding * 3) 0;
	background: map-get($colours, 'white');
	color: $font-colour-primary;
	@include respond-to($max, medium) {
		padding: $site-padding 0;
	}
}
.section--fullscreen {
	position: relative;
	overflow: hidden;
	padding: 0;
	height: auto;
	background-color: map-get($colours, 'white');
	background-blend-mode: multiply;
	@include respond-to($min, medium) {
		height: 100vh;
	}	
}
.section--fullscreen-50 {
	@include respond-to($min, medium) {
		height: 50vh;
	}
}
.section--fullscreen-60 {
	@include respond-to($min, medium) {
		height: 60vh;
	}
}
.section--fullscreen-70 {
	@include respond-to($min, medium) {
		height: 70vh;
	}
}
.section--fullscreen-80 {
	@include respond-to($min, medium) {
		height: 80vh;
	}
}