.content--circle {
    &::before {
        content: ' ';
        display: block;
        padding-top: 100%;
        @include respond-to($max, medium) {
            padding-top: 0%;
        }
    }
    .circle--inner {
        position: absolute;
        /*top: 15%;
        left: 15%;
        width: 70%;
        height: 70%;*/
        top: 5%;
        left: 5%;
        width: 90%;
        height: 90%;
        border-radius: 100%;
        background: map-get($colours, 'white');
        text-align: center;
        padding: $site-padding;
        display: flex;
        flex-direction: column;
        justify-content: center;
        box-sizing: border-box;
        transition: transform 0.2s;
        /*&.larger {
            top: 5%;
            left: 5%;
            width: 90%;
            height: 90%;
        }*/
        &:hover {
            transform: scale(1.05);
        }
        @include respond-to($max, medium) {
            position: relative;
            left: 0;
            top: 0;
            width: 100%;
            height: auto;
            border-radius: 0;
        }
    }
    h4 {
        color: $font-colour-highlight;
        margin: 10px 0;
    }
    p {
        color: map-get($colours, 'grey');
    }
    .green {
        font-size: 30px;
        font-family: $font-family-alternative;
        color: $font-colour-highlight;
        line-height: 1;
        padding: 10px 0;
        word-break: break-word;
        display: inline;
        border-bottom: solid 1px $border-colour-primary;
        margin: 0 auto;
        /*&.larger {
            font-size: 40px;
        }*/
        @include respond-to($max, medium) {
            font-size: 20px;
        }
    }
}