//Define header navigation here

.content--nav-main {
	li {
		vertical-align: bottom;
		padding: 0;
		line-height: 1;
	}
	a {
		color: $font-colour-menu;
		font-weight: 800;
		font-size: $font-size-primary;
	}
	.menu-item-has-children {
		position: relative;
	}
	.sub-menu {
		position: absolute;
		top: 50px;
		display: none;
		padding: ($site-padding / 2) 0;
		li {
			display: inline;
			vertical-align: top;
		}
		ul {
			display: none;
		}
	}
}
.content--nav-main {
	text-align: right;
	li {
		vertical-align: bottom;
        padding: 0 ($site-padding * 1.5);
		line-height: 1;
	}
	a {
		color: $font-colour-menu;
		font-weight: 600;
        text-transform: uppercase;
		font-size: $font-smaller;
		transition: color 0.3s;
		&:hover {
			color: darken($font-colour-menu, 30%);
		}
	}
	.current-menu-item {
		padding: 0;
		a {
			color: darken($font-colour-menu, 30%);
			padding: 8px ($site-padding * 1.5) - 1;
			border: solid 1px $border-colour-primary;
			border-radius: 30px;
		}
	}
	@include respond-to($max, large) {
        text-align: center;
		li {
			padding: 0 $site-padding;
		}
		.current-menu-item a { 
			padding: 8px $site-padding - 1;
		}
    }
	@include respond-to($max, medium) {
		li {
			vertical-align: middle;
			&:not(.current-menu-item) {
				padding: $site-padding ($site-padding / 2);
			}
		}
	}
}