footer {
    .widget-area {
        padding: ($site-padding * 3) 0;
        border-top: dotted 2px #e6e6e6;
        h3 {
            font-size: $font-small;
            padding-bottom: 20px;
        }
        li {
            display: block;
        }
    }
}
.footer-utility {
    padding: ($site-padding * 5) 0;
    background: map-get($colours, 'dark-grey');
}
footer {
    .widget-area {
        padding: ($site-padding * 3) 0;
        border-top: none;
        h3 {
            font-size: $font-large;
            padding-bottom: 20px;
        }
        li {
            display: block;
            border-top: solid $background-colour-primary 1px;
            padding: $site-padding 0;
            &:last-of-type {
                border-bottom: solid $background-colour-primary 1px;
            }
        }
        @include respond-to($max, medium) {
            padding: $site-padding 0;
        }
    }
}
.footer-utility {
    background: none;
    border-top: solid $background-colour-primary 1px;
    padding: ($site-padding * 5) 0;
    text-transform: uppercase;
    font-size: $font-smaller;
    color: map-get($colours, 'grey');
    li {
        padding: 0 0 ($site-padding * 2);
    }
    span {
        font-size: 40px;
    }
    p {
        padding: 0 0 ($site-padding * 2);
    }
}