
.content--nav-utility {
	a {
		font-size: $font-size-alternative;
		font-weight: 100;
	}
	span {
		font-size: 20px;
	}
}
.content--nav-utility a span {
    color: $font-colour-highlight;
    font-size: 24px;
    &::before {
        margin: 0;
    }
}
.content--tagline p {
    text-transform: uppercase;
    color: $font-colour-highlight;
    font-size: 0.7em;
    font-family: $font-family-alternative;
    padding-top: 15px;
}