$small: 600px;
$medium: 800px;
$large: 1000px;
$vlarge: 1200px;

$max: max-width;
$min: min-width;

$site-width: $vlarge;

$site-padding: 10px;

$font-very-large: 2.827em;
$font-larger: 2.2em;
$font-large: 1.999em;
$font-medium: 1.414em;
$font-small: 1.2em;
$font-smaller: 1em;
$font-very-small: 0.777em;

$font-family-primary: 'Questrial', sans-serif;

$font-weight-primary: 400;
$font-weight-alternative: 200;
$font-weight-strong: 800;

$font-size-primary: $font-smaller;
$font-size-heading: $font-large;
$font-size-alternative: $font-very-small;

$font-family: (
	'regular': 'Open Sans'
);

$letter-spacing: 1;
$line-height: 1.4;

$colours: (
	'black': #111,
	'white': #fff,
	'dark-grey': #282828,
	'grey': #999999,
	'light-grey': #f2f2f2,
	'red': #d75454,
	'blue': #4d65e1,
	'yellow': #f2e841,
	'green': #77c66a,
	'purple': #7b4ca7
);

$background-colour-primary: map-get($colours, 'light-grey');
$background-colour-alternative: map-get($colours, 'grey');
$background-colour-highlight: map-get($colours, 'yellow');
$background-colour-menu: map-get($colours, 'white');

$font-colour-primary: map-get($colours, 'black');
$font-colour-alternative: map-get($colours, 'grey');
$font-colour-highlight: map-get($colours, 'blue');
$font-colour-menu: $font-colour-alternative;
$font-colour-link: $font-colour-primary;

$colours-setup: (
	(background-colour-primary, $background-colour-primary, $font-colour-primary),
	(background-colour-alternative, $background-colour-alternative, null),
	(background-colour-highlight, $background-colour-highlight, map-get($colours, 'black')),
	(background-colour-utility, map-get($colours, 'dark-grey'), map-get($colours, 'grey')),
);
	//$class, background colour, text colour
