//Overrides go here, keep this to a minimum, remember to stay dry. IE styles also go here.
.ie .section {
	display: block;
}

.ie .ie-float-right {
	float: right;
}
.set-pad-left-sp2 {
    padding-left: $site-padding * 2; 
}
.set-pad-right-sp2 {
    padding-right: $site-padding * 2; 
}
.set-pad-right-sp {
    padding-right: $site-padding; 
}
.section--fullscreen {
    h1, h2 { color: map-get($colours, 'white'); }
}
.wp-switch-editor, .ed_button {
    width: auto;
}
.dwqa-views-count, .dwqa-votes-count, .dwqa-question-vote, .dwqa-answer-vote, .dwqa-pick-best-answer {
    display: none !important;
}
.dwqa-answers-count {
    padding: 0 10px !important;
}
.dwqa-answer-content p {
    padding-bottom: 20px;
}
.dwqa-captcha {
    text-align: left;
}
.dwqa-question-actions {
    @include respond-to($max, medium) {
        width: 100%;
    }
}

.button--intro-cta {
    @include respond-to($max, medium) {
        padding: 7px;
        margin: 7px 0;
        &:hover {
            padding: 7px;
        }
    }
}

.section--utility {
    .col--1-of-2 {
        @include respond-to($max, medium) {
            &:first-of-type {
                width: 60%;
                max-width: 60%;
            }
            width: 40%;
            max-width: 40%;
        }
    }
}

.widget-area--sidebar {
    margin: $site-padding 0;
    h3 {
        display: inline-block;
        border-bottom: solid 1px $border-colour-primary;
    }
    li {
        display: block;
    }
}

.alignright {float: right; margin-left: 15px;}
.alignleft {float: left; margin-right: 15px;}
.aligncenter {display: block; margin: 0 auto;}

.post_grid {
	padding-bottom: 0;
}

@include respond-to($max, medium) {
	.vertical-center { padding: 100px 0; }
}