@import "header/_header-navigation-static";
@import "header/_utility-navigation";
#header {
    border: solid darken($background-colour-primary, 5%);
    border-width: 1px 0;
    padding: ($site-padding * 2) 0;
}
#logo {
	img {
		max-width: 205px;
		border: 0 none;
	}
	h2, p {
		padding: 0;
	}
	@include respond-to($max, large) {
        text-align: center;
    }
}