// All things type.

body {
	font-style: normal;
	font-family: map-get($font-family, 'Regular'), Sans-serif;
	text-decoration: none;
	letter-spacing: $letter-spacing;
	line-height: $line-height;
}
%default {	
	margin: 0;
	padding: ($site-padding / 2) 0;
	&:first-of-type {
		padding: 0 0 ($site-padding / 2);
	}
}
h1 {
	@extend %default;
	font-size: ($font-size-heading * 1.5);
}
h2 {
	@extend %default;
	font-size: $font-size-heading;
}
h3 {
	@extend %default;
	font-size: ($font-size-heading / 1.5);
}
p {
	font-weight: $font-weight-primary;
	@extend %default;
	font-size: $font-size-primary;
	&:last-of-type {
		padding: ($site-padding / 2) 0 0;
	}
	&:only-of-type {
		padding: 0;
	}
}
a {
	font-size: $font-size-primary;
	color: $font-colour-link;
	text-decoration: none;
	&:hover {
		color: lighten( $font-colour-link, 50% );
	}
}
body {
	font-style: normal;
	font-family: $font-family-primary;
	text-decoration: none;
	letter-spacing: $letter-spacing;
	line-height: $line-height;
}
%heading {	
    color: $font-colour-primary;
	font-family: $font-family-alternative;
	font-weight: 400;
}
h1 {
    @extend %heading;
    font-size: ($font-size-heading * 1.5);
    @include respond-to($max, large) {
        font-size: $font-size-heading;
    }
    @include respond-to($max, medium) {
        font-size: ($font-size-heading / 1.4);
    }
}
h2 {
    @extend %heading;
    font-size: ($font-size-heading * 1.2);
    @include respond-to($max, large) {
        font-size: ($font-size-heading / 1.2);
    }
    @include respond-to($max, medium) {
        font-size: ($font-size-heading / 1.6);
    }
}
h3 {
    @extend %heading;
    font-size: ($font-size-heading / 1.6);
    @include respond-to($max, large) {
        font-size: ($font-size-heading / 2);
    }
}
h4 {
    @extend %heading;
    font-size: ($font-size-heading / 2);
    @include respond-to($max, large) {
        font-size: ($font-size-heading / 2.2);
    }
}
h5 {
    @extend %heading;
    font-size: ($font-size-heading / 2.3);
    margin: 0;
}
p i {
    font-family: $font-family-alternative;
    font-size: 18px;
    font-style: italic;
    color: $font-colour-alternative;
}
article p i {
    font-family: $font-family-primary;
    font-size: $font-size-primary;
    color: $font-colour-primary;
}
a {
	&:hover {
		color: $font-colour-primary;
        transition: color 0.2s;
	}
    &.title {
        color: $font-colour-primary;
    }
}
.icon-bookmark-empty {
    font-size: 90px;
    color: $font-colour-highlight;
}