//Default global styles here

body, figure {
	margin: 0;
	padding: 0;
}
div { box-sizing: border-box; }

img {
	width: auto;
	height: auto;
	max-width: 100%;
}	

.content { 
	position: relative;
	padding: $site-padding;
	width: 100%;
}
ul {	
	margin: 0;
	padding: 0;
	li {
		list-style: none;
		display: inline-block;
		width: auto;
		padding: ($site-padding / 2) 0;
	}
}
nav li { 
	margin: 0 ($site-padding / 2);
}
.set-right nav li, .set-left nav li {
	margin: 0;
}
hr {
	width: 100%;
	margin: 5px 0;
	border: none;
	border-bottom: solid 1px #ddd;
}
input {
	padding: 10px;
	border: solid 1px #ddd;
}
sup {
	color: darken(map-get($colours, 'grey'), 20%);
	font-weight: $font-weight-strong;
}
.wp-caption {
    border: solid 1px $border-colour-primary;
    padding: $site-padding;
    margin: 0 $site-padding $site-padding 0;
    text-align: center;
    float: left;
    p {
        padding-top: $site-padding !important;
        color: $font-colour-alternative;
        font-size: $font-smaller;
    }
    img {

    }
}
.overflow-hidden {
    overflow: hidden;
    height: calc(100% - 50px);
