  font-family: 'fontello';
  src: url('font/fontello.eot?25508112');
  src: url('font/fontello.eot?25508112#iefix') format('embedded-opentype'),
       url('font/fontello.woff2?25508112') format('woff2'),
       url('font/fontello.woff?25508112') format('woff'),
       url('font/fontello.ttf?25508112') format('truetype'),
       url('font/fontello.svg?25508112#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
