/*
	Mind how you go here.
	#** Quickly navigation between related items.
*/

.grid {
	width: 100%;
	max-width: $site-width;	
	display: block;
	display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    flex-flow: row wrap;  
    padding: 0 ($site-padding);  
    margin: 0 auto;
    .grid {
		padding: 0;
	}
	// Stop any child grids having padding.
}
.grid--reverse {
	flex-direction: row-reverse;
}
// Remove max-width from grid.
.section--full-width .grid, .grid--full-width {
	max-width: none;
}
// Column setup, used in col (auto) and mixins further down. #GC
%col {
	position: relative;
	display: inline-block;
	display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex; 
    flex-direction: column;
    width: auto;
    margin: 0;
    padding: 0;
    text-align: left;
    box-sizing: border-box;
    vertical-align: top;
}
// Auto sizing column. #GC
.col {
	@extend %col;
	-webkit-box-flex: 1;
    -webkit-flex-grow: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}
// Define breakpoints for media size queries.
$breakpoints: ( // #BP
	'small': 	$small,
	'medium': 	$medium,
	'large':	$large
);
// Define column sizes with this map.
$columns: (	// #GC
	'11-of-12':	  91.6%,
	'10-of-12':	  83.3333%,
	'6-of-12':	  50%,	
	'5-of-12':	  41.6666%,
	'1-of-12':	  8.3333%,
	'8-of-10':	  80%,
	'1-of-10':	  10%,
	'4-of-6':	  66.6666%,
	'3-of-6':	  50%,
	'2-of-6':	  33.3333%,
	'1-of-6':	  16.6666%,
	'1-of-5':     20%,
	'2-of-5':     40%,
	'3-of-5':     60%,
	'4-of-5':     80%,	
	'1-of-4':     25%,
	'2-of-4':     50%,
	'3-of-4':     75%,
	'1-of-3':     33.3333%,
	'2-of-3':     66.6666%,
    '1-of-2':     50%,
    '1-of-1':     100%,
    'full-width': 100%
);

// This sets the media query up for each breakpoint. You can pass $max or $min to set the query to min-width or max-width. #BP
@mixin respond-to($direction, $name) {
	@if map-has-key($breakpoints, $name) {
		@media ($direction:#{map-get($breakpoints, $name)}) {
			@content;
		}
	}
}
@each $column, $columnspan in $columns {
    .col--#{$column} {
        @extend %col; // #GC
        width: $columnspan;
        max-width: $columnspan;
    } 
    // IE8 will default (%col) to display: inline-block, we need to allow for the white space with -0.5% on all column sizes.
    .ie .col--#{$column} {
        @extend %col;
        width: $columnspan - 0.5%;
        max-width: none;
    }   
}
@each $column, $columnspan in $columns {
	 @include respond-to($max, large) { // #BP
		.col--large-hide { display: none; }
        .col--large-#{$column} {
	        width: $columnspan;
	        max-width: $columnspan;
	    }   
    }
	@include respond-to($max, medium) {
		.col--medium-hide { display: none; }
		// Default column collapse
        .col--#{$column} {
	        @if $columnspan > 50% {
	            width: 100%;
	            max-width: 100%;
            } @else {
	            width: 50%;
	            max-width: 50%;
            }
        }
        .col--medium-#{$column} {
	        width: $columnspan;
	        max-width: $columnspan;
	    }  
    }
	@include respond-to($max, small) {
		.col--small-hide { display: none; }
        .col--#{$column} {
            width: 100%;
            max-width: 100%;
        }
        .col--small-#{$column} {
	        width: $columnspan;
	        max-width: $columnspan;
	    } 
    }
}
